import uniList from '../../components/uni-list/uni-list.vue'
import uniItem from '../../components/uni-list-item/uni-list-item.vue'
export default {
	data() {
		return {
			waitTime:'',
			avatarUrl: '',
			name: '',
			isVip: '2',
			superiorId: '',
			openId: ''
		}
	},
	onShow() {
		uni.getSetting({
			success:res=>{
				if (res.authSetting['scope.userInfo']){
					this.getUserInfo()
				}
			}
		})
		uni.getStorage({
			key: 'openId',
			success: res => {
				this.openId = res.data
				// uni.request({
				// 	url: 'https://www.finetwm.com/xmRepair/userInfo/isvip',
				// 	data: {
				// 		openid: 'o4__y5Cy6VPWrggg_YCQX81qvdAU',
				// 		superiorId: this.superiorId
				// 	},
				// 	success: res => {
				// 		console.log(res)
				// 		this.isVip = res.data.data.isvip
				// 		this.waitTime = res.data.data.wait
				// 	}
				// })
			}
		})
	},
	onLoad() {
		
	},
	methods: {
		getUserInfo(){
			uni.getUserInfo({
				success:res=>{
					this.avatarUrl = res.userInfo.avatarUrl
					this.name = res.userInfo.nickName
				}
			})
		},
		
		onBeVip() {
			uni.navigateTo({
				url: '../bevip/bevip',
				success: res => {
					console.log('success')
				}
			});
		},
		onOrderList() {
			uni.navigateTo({
				url: '../orderlist/orderlist'
			});
		},
		onMyWallet() {
			uni.navigateTo({
				url: '../mywallet/mywallet'
			});
		},
		onAddressList() {
			uni.navigateTo({
				url: '../addresslist/addresslist'
			});
		},
		onBusinessIn() {
			uni.navigateTo({
				url: 'business-in'
			})
		},
		onFriends() {
			uni.navigateTo({
				url: 'my-friends'
			})
		},
		onCards() {
			uni.navigateTo({
				url: 'cards'
			})
		},
		onlineKf() {
			uni.navigateTo({
				url: 'online-kf'
			})
		}
